// Importa variÃ¡veis globais com um namespace especÃ­fico
@use '../config/variables' as vars;

// Cores
$nav-text-color: vars.$text-color;
$nav-hover-color: vars.$primary-color;
$nav-bg-color: vars.$white;
$nav-border-color: rgba(0, 0, 0, 0.05);
$nav-active-indicator: vars.$primary-color;

// EspaÃ§amentos
$nav-item-padding: 1rem 1.25rem;
$nav-submenu-padding: 0.5rem 0;
$nav-submenu-item-padding: 0.5rem 1.5rem;
$nav-indent-level: 1rem;

// TransiÃ§Ãµes
$nav-transition: all 0.25s cubic-bezier(0.25, 0.46, 0.45, 0.94);

// Sombras
$nav-box-shadow: 0 5px 25px rgba(0, 0, 0, 0.1);
$nav-mobile-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);

// Bordas
$nav-border-radius: 0 0 6px 6px;
$nav-active-border-radius: 3px 3px 0 0;

// Z-index
$nav-z-index: 1000;
$nav-mobile-z-index: 100;

// Breakpoints
$nav-mobile-breakpoint: 991px;

// Ãcones
$nav-arrow-down: '+';
$nav-arrow-up: '-';
$nav-indicator-width: 50%;
$nav-indicator-height: 3px;
